/* global jQuery TimelineLite Waypoint */
/*
 * Slide form down on mobile when button is clicked
 */
(function ($) {

	"use strict";

	var form__wrapper = document.querySelector(".form__wrapper") || null;
	var ease = window.Power4.easeInOut;

	function showForm() {
		var tShow = new TimelineLite();
		tShow.addLabel("start");
		tShow.fromTo(form__wrapper, 1, { y: "-100%", opacity: 0 }, { y: "0%", opacity: 1, ease: ease }, "start+=0");
	}

	function hideForm() {
		$(".form__wrapper").removeClass("overlay");
		var tHide = new TimelineLite();
		tHide.addLabel("start");
		tHide.fromTo(form__wrapper, 1, { y: "0%", opacity: 1 }, { y: "-100%", opacity: 0, ease: ease},  "start+=0");
	}

	if (form__wrapper !== null) {
		$(".heroContainer .q-button, .try-it .q-button").on("click", function () {
			$(".form__wrapper").addClass("overlay");
			showForm();
		});

		$(".close-overlay").on("click", function () {
			hideForm();
		});
	}

}(jQuery));

/*
 * animate heading and computers
 */
(function () {

	"use strict";

	var headingAnimated = false;

	new Waypoint({
		element: document.getElementById('features'),
		offset: "80%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !headingAnimated) {
				t1.fromTo(".feature-container .heading--2", 1, { y: "-10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: window.Power4.easeInOut });
			}

			headingAnimated = true;
		}
	});

	var computersDesktopAnimated = false;

	new Waypoint({
		element: document.getElementById('computers'),
		offset: "80%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !computersDesktopAnimated) {
				t1.fromTo(".screenshot.desktop", 1.5, { x: "25%", autoAlpha: 0 }, { x: "0%", autoAlpha: 1, ease: window.Power4.easeInOut });
			}

			computersDesktopAnimated = true;
		}
	});


	var laptopMobileAnimated = false;

	new Waypoint({
		element: document.getElementById('laptop-mobile'),
		offset: "70%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !laptopMobileAnimated) {
				t1.fromTo(".screenshot.mobile", 1.5, { x: "25%", autoAlpha: 0 }, { x: "0%", autoAlpha: 1, ease: window.Power4.easeInOut });
			}

			laptopMobileAnimated = true;
		}
	});

}());